.grid {
  width: 70%;
  margin: 0 auto;
  display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 10vh, repeat(3, 10vh);
    grid-gap: 15px;
}

h1, 
h2 {
  font-family: 'Amatic SC', cursive;
}

h1 {
  font-size: 36pt;
  float: left;
  text-align: left;
}

h2 {
  font-size: 24pt;
  float: right;
  text-align: right;
}

.header {
  padding-top: 15px;
  grid-column-start: 1;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
